let that;
import {delGoodsName} from '@/axios/index.js'
export default {
	data() {
		return {
			goodsObj: ''
		}
	},
	onLoad(options) {
		that = this;
		that.goodsObj = JSON.parse(uni.getStorageSync('goodsObj'))
		console.log(that.goodsObj)
	},
	methods: {
		del: function() {
			uni.showModal({
				title: '提示',
				content: '确定删除吗？',
				success: function(res) {
					if (res.confirm) {
						console.log('用户点击确定');
						that.delGoods(that.goodsObj.id)
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		delGoods: function(id) { //删除
			delGoodsName({
				id
			}).then(res =>{
				console.log(res)
				if(res.ret == 0) {
					uni.$emit('getGoods')
					uni.showToast({
						title: '删除成功',
						duration: 2000
					});
					that.$Router.back(1)
				}else {
					uni.showToast({
						title: '删除失败，请重试',
						duration: 2000
					});
				}
			})
		},
		edit: function() {//编辑
			var isCanPrivate = that.goodsObj.isCanPrivate
			var id = that.goodsObj.id
			that.$Router.push({name: 'edit_goods',params:{isCanPrivate,id,isAtlas:'true'}})
		
		},
		ChooseImage: function(url) {
			var arr = []
			that.goodsObj.imgList.forEach((el,i)=>{
				arr.push(el.url)
			})
			uni.previewImage({
				urls: arr,
				current: url
			});
		},
	},
	filters: {
		time: function(arg) {
			
		}
	}
}
